import Header from '@/components/Header';
import { Filter, SquareKanban, Search, TrendingDown, PlusSquare, Share2, SquarePen, Table, SquareChartGantt } from 'lucide-react';
import Link from 'next/link';
import React, { useState } from 'react';
import ModalNewPart from "./ModalNewPart";
import ModalEditPart from "./ModalEditPart";
import { Part, useGetPartsQuery, useGetProgramsQuery, useGetUsersQuery } from '@/state/api';

type Props = {
    activeTab: string;
    setActiveTab: (tabname: string) => void
    activePart?: Partial<Part> | null;
    searchQuery: string;
    setSearchQuery: (query: string) => void;
    includeChildren: boolean;
    setIncludeChildren: (value: boolean) => void;
};

const PartHeader = ({
    activeTab,
    setActiveTab,
    activePart,
    searchQuery,
    setSearchQuery,
    includeChildren,
    setIncludeChildren,
}: Props) => {
    const [isModalNewPartOpen, setIsModalNewPartOpen] = useState(false); 
    const [isModalEditPartOpen, setIsModalEditPartOpen] = useState(false);
    const { data: users = [] } = useGetUsersQuery();
    const { data: programs = [] } = useGetProgramsQuery();
    const { data: parts = [] } = useGetPartsQuery(undefined, { skip: !activePart?.parentId });
    
    const headerTitle = activePart
        ? `${activePart.partName} (${activePart.code})`
        : "My Part";
    
    const assignedUser = activePart?.assignedUserId 
        ? users.find(user => user.id === activePart.assignedUserId)
        : null;
    const activeProgramName = activePart
        ? activePart.program?.name ?? programs.find(program => program.id === activePart.programId)?.name
        : undefined;
    const parentPart = activePart
        ? activePart.parent ?? parts.find(part => part.id === activePart.parentId)
        : undefined;
    const parentDisplay = parentPart
        ? `${parentPart.partName} (${parentPart.code})`
        : undefined;
    
    return (
        <div className="px-4 xl:px-6">
            <ModalNewPart
                isOpen={isModalNewPartOpen}
                onClose={() => setIsModalNewPartOpen(false)}
            />
            <ModalEditPart
                isOpen={isModalEditPartOpen}
                onClose={() => setIsModalEditPartOpen(false)}
                part={activePart ? { ...activePart, id: activePart.id ?? 0 } : null}
            />
            <div className="pb-6 pt-6 lg:pb-4 lg:pt-8">
                <Header name={headerTitle}
                    buttonComponent={
                        <div className="flex gap-3">
                        {/* New Part Button */}
                        <button
                            className="flex items-center rounded-md bg-blue-primary px-3 py-2 text-white hover:bg-blue-600"
                            onClick={() => setIsModalNewPartOpen(true)}
                        >
                            <PlusSquare className="mr-2 h-5 w-5" />New Part
                        </button>
                        {/* Edit Part Button */}
                        <button
                            className="flex items-center rounded-md bg-gray-300 px-3 py-2 text-gray-800 hover:bg-gray-400 dark:bg-dark-tertiary dark:text-white"
                            onClick={() => setIsModalEditPartOpen(true)}
                            disabled={!activePart}
                        >
                            <SquarePen className="mr-2 h-5 w-5" />Edit Part
                        </button>
                        </div>
                    }
                />
                {activePart && (
                    <div className="mt-2 flex flex-wrap items-center gap-x-4 gap-y-1 text-sm text-gray-600 dark:text-gray-400">
                        <span>
                            Parent:{' '}
                            {parentPart ? (
                                <Link
                                    href={`/parts/${parentPart.id}`}
                                    className="text-blue-600 hover:underline dark:text-blue-400"
                                >
                                    {parentDisplay}
                                </Link>
                            ) : (
                                'None'
                            )}
                        </span>
                        <span>
                            Program:{' '}
                            {activeProgramName ? (
                                <Link
                                    href="/programs"
                                    className="text-blue-600 hover:underline dark:text-blue-400"
                                >
                                    {activeProgramName}
                                </Link>
                            ) : (
                                'Unassigned'
                            )}
                        </span>
                        <span>
                            Assignee:{' '}
                            {assignedUser?.username ? (
                                <Link
                                    href={`/users/${assignedUser?.id ?? activePart?.assignedUserId}`}
                                    className="text-blue-600 hover:underline dark:text-blue-400"
                                >
                                    {assignedUser?.name ?? 'View Assignee'}
                                </Link>
                            ) : (
                                'Unassigned'
                            )}
                        </span>
                    </div>
                )}
            </div>

            {/* TABS */}
            <div className="flex flex-wrap-reverse gap-2 border-y border-gray-200 pb-[8px] pt-2 dark:border-stroke-dark md:items-center">
                <div className="flex flex-1 items-center gap-2 md:gap-4">
                    <TabButton
                        name="Board"
                        icon={<SquareKanban className="h-5 w-5" />}
                        setActiveTab={setActiveTab}
                        activeTab={activeTab}
                    />
                    <TabButton
                        name="Table"
                        icon={<Table className="h-5 w-5" />}
                        setActiveTab={setActiveTab}
                        activeTab={activeTab}
                    />
                    <TabButton
                        name="Burndown"
                        icon={<TrendingDown className="h-5 w-5" />}
                        setActiveTab={setActiveTab}
                        activeTab={activeTab}
                    />
                    <TabButton
                        name="Timeline"
                        icon={<SquareChartGantt className="h-5 w-5" />}
                        setActiveTab={setActiveTab}
                        activeTab={activeTab}
                    />
                </div>
                <div className="flex items-center gap-2">
                    <label className="flex items-center gap-2 rounded-md border border-gray-200 px-3 py-2 text-sm text-gray-600 dark:border-dark-secondary dark:text-neutral-400">
                        <input
                            type="checkbox"
                            className="h-4 w-4 accent-blue-600"
                            checked={includeChildren}
                            onChange={(e) => setIncludeChildren(e.target.checked)}
                        />
                        Include children
                    </label>
                    <button className="text-gray-500 hover:text-gray-600 dark:text-neutral-500 dark:hover:text-gray-300">
                        <Filter className="h-5 w-5" />
                    </button>
                    <button className="text-gray-500 hover:text-gray-600 dark:text-neutral-500 dark:hover:text-gray-300">
                        <Share2 className="h-5 w-5" />
                    </button>
                    <div className="relative">
                        <input 
                            type="text" 
                            placeholder="Search Work Item"
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            className="rounded-md border py-1 pl-10 pr-4 focus:outline-none dark:border-dark-secondary dark:bg-dark-secondary dark:text-white" 
                        />
                        <Search className="absolute left-3 top-2 h-4 w-4 text-gray-400 dark:text-neutral-500" />
                    </div>
                </div>
            </div>
        </div>
    )
};

type TabButtonProps = {
    name: string;
    icon: React.ReactNode;
    setActiveTab: (tabName: string) => void;
    activeTab: string;
};

const TabButton = ({ name, icon, setActiveTab, activeTab}: TabButtonProps) => {
    const isActive = activeTab === name;

    return (
        <button
            className={`relative flex items-center gap-2 px-1 py-2 text-gray-500 after:absolute after:-bottom-[9px] after:left-0 after:h-[1px] after:w-full hover:text-blue-600 dark:text-neutral-500 dark:hover:text-white sm:px-2 lg:px-4 ${
        isActive ? "text-blue-600 after:bg-blue-600 dark:text-white" : ""
            }`}
            onClick={() => setActiveTab(name)}
            >
                {icon}
                {name}
            </button>
    )

}

export default PartHeader;